.dropdown{
    position: relative;
    z-index: 99;

    &__toggle{
        border: 0;
        outline: 0;
        background-color: transparent;
        position: relative;

        & > i{
            font-size: 2.5rem;
            color: var(--txt-color);
        }

        &-badge{
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: -12px;
            right: -10px;
            height: 25px;
            width: 25px;
            border-radius: 50%;
            background-color: var(--main-color);
            font-size: 0.8rem;
            color: white;
        }
    }  
    &__content{
        position:absolute;
        top: calc(100% + 5px);
        right: 0;
        width: max-content;
        max-width: 400px;
        background-color: var(--main-bg);
        box-shadow: var(--box-shadow);
        border-radius: var(--border-radius);
        overflow: hidden;
        transform-origin: top right;
        transform: scale(0);
        transition : transform 0.3s ease 0s;
        &.active{
            transform: scale(1);
            transition: transform 0.5s var(--transition-cubic);
        }
    }
    &__footer{
        padding: 20px;
        text-align: center;
    }
}

